import React, { FC } from 'react';
import { ItemClickType } from '@enum';
import { CommonProps } from '@interface';
import './index.less';

const Des: FC<CommonProps> = (props = {}) => {
    const {
        schema: {
            className = '',
            size = 'size-16', // 文案大小 size-12 | size-14 | size-16
            lineClamp = 2, // 一行or两行
            text,
            logInfo: { itemClickType = ItemClickType.CARD_DES_CLICK } = {},
        },
        style = {},
        info: { adDescription = '' },
    } = props;
    const desc = text || adDescription;
    return desc ? (
        <div
            data-itemClickType={itemClickType}
            className={`des ${className} ${size} line-${lineClamp}`}
            style={style}
        >
            {desc}
        </div>
    ) : null;
};

export default Des;
